import Server from './server';

class API extends Server {
    /**
     *  用途：获取首页data列表
     *  @method post
     *  @return {promise}
     */
    async getLogin(method = 'post', url = 'loginData.json', params = {}) {
        try {
            let result = await this.axios(method, url, params);
            if (result) {
                return result;
            } else {
                {
                    let err = {
                        tip: '登录失败',
                        response: result,
                        data: params,
                        url: url,
                    };
                    throw err;
                }
            }
        } catch (err) {
            throw err;
        }
    }
    /**
     *  用途：获取首页data列表
     *  @method post
     *  @return {promise}
     */
    async getData(method = 'post', url = 'pointFree.json', params = {}) {
        try {
            let result = await this.axios(method, url, params);
            if (result) {
                return result;
            } else {
                {
                    let err = {
                        tip: '获取列表信息失败',
                        response: result,
                        data: params,
                        url: url,
                    };
                    throw err;
                }
            }
        } catch (err) {
            throw err;
        }
    }
    /**
     *  用途：获取分号列表
     *  @method post
     *  @return {promise}
     */
    async getShopList(method = 'post', url = 'shopList.json', params = {}) {
        try {
            let result = await this.axios(method, url, params);
            if (result) {
                return result;
            } else {
                {
                    let err = {
                        tip: '获取分号信息失败',
                        response: result,
                        data: params,
                        url: url,
                    };
                    throw err;
                }
            }
        } catch (err) {
            throw err;
        }
    }
    /**
     *  用途：获取承兑商充值方式
     *  @method post
     *  @return {promise}
     */
    async getShopRechargeStyle(method = 'post', url = 'shopRechargeStyle.json', params = {}) {
        try {
            let result = await this.axios(method, url, params);
            if (result) {
                return result;
            } else {
                {
                    let err = {
                        tip: '获取分号充值方式失败',
                        response: result,
                        data: params,
                        url: url,
                    };
                    throw err;
                }
            }
        } catch (err) {
            throw err;
        }
    }
    /**
     *  用途：获取充提记录
     *  @method post
     *  @return {promise}
     */
    async getRechargeWithdrawRecord(method = 'post', url = 'recordList.json', params = {}) {
        try {
            let result = await this.axios(method, url, params);
            if (result) {
                return result;
            } else {
                {
                    let err = {
                        tip: '获取充提记录失败',
                        response: result,
                        data: params,
                        url: url,
                    };
                    throw err;
                }
            }
        } catch (err) {
            throw err;
        }
    }
}

export default new API();


