import * as home from './action-type';

// 获取市场币种信息
export const marketCoin = (state = { coinDetail: [] } , action = {}) => {
  switch(action.type){
    case home.GETCOIN:
      return {...state, [action.datatype]: action.coinDetail};
    default:
      return state;
  }
};
// 获取承兑商信息
export const shopList = (state = { shopList: [] } , action = {}) => {
    switch(action.type){
        case home.GETSHOPLIST:
            return {...state, shopList: action.shopList};
        default:
            return state;
    }
};
// 获取用户登录信息
export const loginData = (state = { loginData: {} } , action = {}) => {
    switch(action.type){
        case home.LOGINDATA:
            return {...state, loginData: action.loginData};
        default:
            return state;
    }
};


