import React, { Component } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect
} from 'react-router-dom'
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import "./assets/iconfonts/iconfont.css";
import API from './api/api';
import * as R from 'ramda'
import { is, fromJS } from 'immutable';
import 'antd/dist/antd.css';
import { getCoin,getShopList,loginData } from './store/home/action';
import asyncComponent from './utils/asyncComponent';
import {getStore} from './utils/utils';
const Login = asyncComponent(() => import("./components/login/Login"));
const Forget = asyncComponent(() => import("./components/forget/Forget"));
const Register = asyncComponent(() => import("./components/register/Register"));
const Market = asyncComponent(() => import("./components/market/Market"));
const Deal = asyncComponent(() => import("./components/deal/Deal"));
const Shop = asyncComponent(() => import("./components/shop/shop/Shop"));
const Recharge = asyncComponent(() => import("./components/shop/recharge/Recharge"));
const Withdraw = asyncComponent(() => import("./components/shop/withdraw/Withdraw"));
const Record = asyncComponent(() => import("./components/shop/record/Record"));
const Method = asyncComponent(() => import("./components/shop/method/Method"));
const Manage = asyncComponent(() => import("./components/shop/manage/Manage"));
class App extends Component {
    static propTypes = {
        dispatchDataList: PropTypes.func.isRequired,
        dispatchShopList: PropTypes.func.isRequired
    };
    constructor(props){
        super(props);
        this.initData = this.initData.bind(this);
    }
    // 初始化数据
    initData = async () => {
        const {dispatchDataList,dispatchShopList} = this.props;
        try{
            let result = await this.fetchData('get');
            dispatchDataList(result,"coinDetail");
            let shopList = await this.fetchShop('get');
            dispatchShopList(shopList);
        }catch(err){
            console.error(err);
        }
    };
    fetchData = (type) => {
        return API.getData(type)
            .then(R.prop('tasks'))
            .then(R.filter(R.propEq('username','Scott')))
            .then(R.reject(R.propEq('complete', true)))
            .then(R.map(R.pick(['key','price', 'coin', 'cny', 'rise','amount'])))
            .then(R.sortBy(R.prop('price')));
    };
    fetchShop = (type) => {
        return API.getShopList(type)
            .then(R.prop('data'))
    };
    shouldComponentUpdate(nextProps, nextState) {
        return !is(fromJS(this.props), fromJS(nextProps))|| !is(fromJS(this.state),fromJS(nextState))
    };
    componentDidMount(){
        this.initData();
    };
    render(){
        return (
            <Router>
                <div>
                    <Switch>
                        <Route exact path="/login" component={Login} />
                        <Route exact path="/forget" component={Forget} />
                        <Route exact path="/register" component={Register} />
                        <Route exact path="/market" component={Market} />
                        <Route exact path="/deal/:coin" component={Deal} />
                        <Route exact path="/shop/shop" component={Shop} />
                        <Route exact path="/shop/recharge/:key" component={Recharge} />
                        <Route exact path="/shop/withdraw/:key" component={Withdraw} />
                        <Route exact path="/shop/record" component={Record} />
                        <Route exact path="/shop/method" component={Method} />
                        <Route exact path="/shop/manage" component={Manage} />
                        {
                            getStore('user') ?  <Redirect from="/" to="/market"/> :  <Redirect from="/" to="/login"/>
                        }
                    </Switch>
                </div>
            </Router>
        )
    }
}


const mapStateToProps = (state) => ({});

const mapDispatchToProps = (dispatch, ownProps) => ({
    dispatchDataList: (a,b) => {
        dispatch(getCoin(a,b))
    },
    dispatchShopList: (list) => {
        dispatch(getShopList(list))
    }
});

export default  connect(
    mapStateToProps,
    mapDispatchToProps
)(App);







