import * as home from './action-type';

// 获取市场币种信息
export const getCoin = (coinDetail, datatype) => {
  return {
    type: home.GETCOIN,
    coinDetail,
    datatype,
  }
};
// 获取承兑商信息
export const getShopList = (shopList) => {
    return {
        type: home.GETSHOPLIST,
        shopList,
    }
};
// 获取登录信息
export const loginData = (loginData) => {
    return {
        type: home.LOGINDATA,
        loginData,
    }
};



